.logo-navbar {
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0; }

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
    height: 3px !important;
    background-color: $dark !important; }

.light-background, .light-background::before, .light-background::after {
    background-color: $light !important;
    color: $dark;
    fill: $light !important; }

.burger-container {
    float: right;
    z-index: 5;
    position: fixed;
    top: 15px;
    right: 15px; }

.hamburger-inner.dark-mode {
    background-color: $light !important; }

.hamburger-inner.dark-mode:after {
    background-color: $light !important; }

.hamburger-inner.dark-mode:before {
    background-color: $light !important; }

.logo-elem.dark-mode {
    fill: $light !important; }

.navbar-container {
    height: 100vh;
    width: 100%;
    display: none;
    z-index: 4;
    position: fixed;
    background-color: $dark;
    color: $light;
    top: 0; }

.navbar {
    display: flex;
    margin: 2vw;
    height: 95%; }

.nav-left {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
.nav-right {
    width: 40%;
    align-self: center;
    margin-bottom: 10vh; }

.navbar-open {
    display: block; }

.navbar-ws-img {
    filter: saturate(0);
    mix-blend-mode: multiply;
    -webkit-filter: saturate(0);
    -webkit-mix-blend-mode: multiply;
    width: 22vw; }


.main-titles-flex h1 {
    margin-bottom: 5vh; }

.main-titles-flex h1:hover {
    font-style: italic; }

.p-relative {
    position: relative; }
.p-absolute {
    position: absolute; }

.pages-flex, .socials-flex {
    justify-content: space-between;
    display: flex; }

.main-titles-flex {
    flex-grow: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-bottom: 7vh; }
